class ContactFormWidget extends Component {
  constructor(props) {
    super(props);
    this.state = {success: false, error: false};
  }

  componentDidMount() {
    window.widget = this;
  }

  render() {
    if (!this.state.success) {
      return (
        <div>
          <Title title="Kontakt - Die Föderalen" url={createUrl('/kontakt')} />
          <TitleRow>
            <Col>
              <h1>Kontakt</h1>
            </Col>
          </TitleRow>
          <ContentRow>
            <Col>
              <Text>
                Um uns eine Nachricht zu schicken, gibt hier einfach deine
                Nachricht ein.
              </Text>
              {this.state.error && (
                <ContentRow>
                  <Col>
                    <Alert variant="danger">{this.state.error}</Alert>
                  </Col>
                </ContentRow>
              )}
            </Col>
          </ContentRow>
          <ContentRow>
            <Col>
              <ContactForm />
            </Col>
          </ContentRow>
          <ContentRow>
            <Col>
              <br />
              <h2>per E-Mail:</h2>
              <h3>Allgemein</h3>
              <p>info [at] die-foederalen.de</p>
              <h3>Kündigung</h3>
              <p>kuendigung [at] die-foederalen.de</p>
              <h3>Spenden</h3>
              <p>spenden [at] die-foederalen.de</p>
            </Col>
          </ContentRow>
        </div>
      );
    } else {
      return (
        <div>
          <Title
            title="Die Nachricht wurde verschickt! - Die Föderalen"
            url={createUrl('/kontakt')}
          />
          <TitleRow>
            <Col>
              <h1>Die Nachricht wurde erfolgreich verschickt!</h1>
            </Col>
          </TitleRow>
          <ContentRow>
            <Col>
              <Text>
                Vielen Dank! Deine Nachricht wurde an den Support geschickt. Wir
                werden dich so schnell wie möglich kontaktieren.
              </Text>
            </Col>
          </ContentRow>
        </div>
      );
    }
  }
}

class Kontakt extends Component<Props> {
  render() {
    return (
      <DefaultLayout navigation={navigation}>
        <ContactFormWidget />
      </DefaultLayout>
    );
  }
}

export default Kontakt;
